@import '../../variables';

.contacts {
  display: flex;

  iframe {
    height: 100%;
    min-height: calc(100vh - 120px - 100px);
    flex: 1; }

  .contacts-container {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;

    .contacts-data {
      height: calc(100% - 200px);
      display: flex;
      flex-direction: column;
      justify-content: center;

      a {
        color: $background_main; }

      .contact-line {
        display: flex;
        align-items: center;
        font-size: 27px;
        margin-bottom: 20px;
        cursor: pointer;
        transition: 0.5s;

        &:hover {
          color: $main_color; }

        label {
          cursor: pointer; }

        svg {
          font-size: 35px;
          margin-right: 5px; } } } } }

@media (max-width: $mobile) {
  .contacts {
    flex-direction: column-reverse;

    .contact-line {
      justify-content: center; } } }
